@import '../../utils/mixins';

.home {
    animation: pageAppearing 4s ease forwards;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: auto;
    width: 100%;
    height: 100vh;
    align-items: center;

    &_title {
        display: flex;
        flex-direction: column;
        row-gap: 20px;
        justify-content: center;
        align-items: center;
        margin: 40px 0;
        
        &_logoMask {
            position: relative;
            width: 50%;
            @include colorDuo3big;
            @media only screen and (min-width: $padMinWidth) and (max-width: $padMaxWidth){
                width: 70%;
                
            }
            @media only screen and (max-width:$mobileMaxWidth){
                width: 70%;
            } 
        }
          
        &_logoMask img {
            animation: float 6s ease-in-out infinite;
            width: 100%;
            height: auto;
            /* Appliquer le masque */
            -webkit-mask: url('../../assets/logoSouffleur.svg#mask');
            mask: url('../../assets/logoSouffleur.svg#mask');
        }

        &_logo {
            width: 300px;
            margin: auto;
            animation: float 6s ease-in-out infinite;
            @include colorDuo3;
        }
        &_text {
            font-family: 'MainFont_light';
            font-size: 17px;
            margin: auto;
        }
        @media only screen and (min-width: $padMinWidth) and (max-width: $padMaxWidth){
            
        }   
        @media only screen and (max-width:$mobileMaxWidth){
        
        }      
    }

    &_nav {
        display: flex;
        flex-direction: column;
        row-gap: 30px;
        margin: 0 auto;
    
        &_menu {
            display: flex;
            flex-direction: row;
            column-gap: 22px;
            @media only screen and (min-width: $padMinWidth) and (max-width: $padMaxWidth){
                column-gap: 17px;
                
            }
            
            @media only screen and (max-width:$mobileMaxWidth){
                font-size: 17px;
                flex-wrap: wrap;
            } 
            &_item, h2, p {
                margin: auto;
                color: $colorDarkGrey;
                font-family: 'MainFont_light';
                font-size: 22px;
                font-weight: normal;
                &:hover {
                    cursor: pointer;
                    font-style: italic;
                    font-weight: bold;
                }
                @media only screen and (min-width: $padMinWidth) and (max-width: $padMaxWidth){
                    font-size: 17px;
                }
                
                @media only screen and (max-width:$mobileMaxWidth){
                    font-size: 17px;
                } 
            }
        }

        &_socials {
            display: flex;
            flex-direction: row;
            column-gap: 22px;
            width: auto;
            border-radius: 50%;
            margin: auto;
            &_item {
                img {
                    width: 30px;
                }
                &:hover {
                    cursor: pointer;
                    // color: $colorMediumGrey;
                    // background-color: pink;
                    @include colorDuo3;
                    // font-style: italic;
                    // font-weight: bold;
                }
            }
        }
        
        &_item, a, button {

            display: flex;
            width: 100%;
            justify-content: center; 
            text-decoration: none;
            border: none;
            background: none;
            font-family: 'MainFont_light';
        }
    }
}