@import '../../utils/mixins';

.editWorkModal {

    &_confirmBox {
        z-index: 1;
        position: fixed;
        align-items: center;
        justify-content: center;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background:rgba(0, 0, 0, 0.8);

        &--displayOn {
            display: flex;
        }

        &--displayOff {
            display: none;
        }

        &_container  {
            display: flex;
            flex-direction: column;
            width: 630px;
            height: auto;
            padding: 3% 0;
            background-color: $colorLightGrey ;

            &_question {
                padding-top: 20px;
                margin: 0 auto 5px auto;
                font-family: 'MainFont_light';
                font-weight: bold;
                font-size: 22px;
                color: $colorDarkGrey;
                display: flex;
                justify-content: center;
                width: 300px;
                padding: 12px;

            }
            &_buttons {
                display: flex;
                flex-direction: row-reverse;
                justify-content: space-between;
                column-gap: 20px;
                width: 15%;
                margin: auto;
                

                button {
                    font-family: 'SpecialFont_regular';
                    border: none;
                    background: none;
                    
                    width: 50%;
                    margin: 5% auto 0 auto;
                    padding: 2%;
                    font-size: 35px;
                    &:hover {
                        cursor: pointer;
                        color: $colorLightGrey;
                        background-color: $colorThree;
                    }
                }
            }
        }
    } 
}
